'use client';

import { useState, useEffect } from 'react';
import { onAuthStateChanged, User } from 'firebase/auth';
import { useRouter } from 'next/navigation';
import { auth } from '../../firebaseConfig'; // Uses your safe config
import FileUploader from '../components/FileUploader';
import MarkdownViewer from '../components/MarkdownViewer';

export default function MaterialToMD() {
  const [user, setUser] = useState<User | null>(null);
  const [loadingAuth, setLoadingAuth] = useState(true);
  
  const router = useRouter();

  // 1. Safe Auth Check
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      if (!currentUser) {
        router.push('/login');
      } else {
        setUser(currentUser);
      }
      setLoadingAuth(false);
    });
    return () => unsubscribe();
  }, [router]);

  // 2. Show a visible "Loading" state so the screen isn't white
  if (loadingAuth) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <h1 className="text-xl font-bold text-blue-600">Checking Access...</h1>
      </div>
    );
  }

  // 3. If no user (and redirected), render nothing
  if (!user) return null;

  return (
    <div className="max-w-6xl mx-auto p-8">
      <h1 className="text-3xl font-bold mb-4">Material to Markdown</h1>
      <p className="mb-8 text-gray-600">Welcome, {user.email}</p>
      
      {/* Basic Placeholders for your components to prevent import crashes */}
      <div className="border p-4 rounded bg-gray-50 text-center text-gray-500">
        File Uploader Component Here
        <br/>
        <input type="file" className="mt-4" />
        <br/>
        <button className="mt-2 bg-blue-600 text-white px-4 py-2 rounded">Convert</button>
      </div>
    </div>
  );
}